.services {
    position: relative;
    width: 100%;
    height: 100vh;
    background-position: center;
    background-size: cover;
    transform: translate3d(0, 0, 0); }

.services-block {
    position: absolute;
    top: 9.1875vw;
    left: 29.6875vw;
    z-index: 1;
    &__txt-roll {
        height: 2.7vw;
        overflow: hidden;
        margin-bottom: 2.5vw; }
    &__txt-item {
        display: inline-block;
        font-family: $avenir-black;
        font-size: 2.625vw;
        color: $white;
        text-transform: uppercase;
        letter-spacing: 1.325vw;
        transform: translate3d(0, 2.5vw, 0); }
    &__line {
        height: 0.1875vw;
        width: 25vw;
        background-color: $orange;
        margin-bottom: 7.375vw;
        transform-origin: 0 100%;
        transform: translate3d(0, 0, 0);
        transform: scaleX(0); }
    &__item {
        cursor: pointer;
        color: #737373;
        text-transform: uppercase;
        font-family: $avenir-black;
        font-size: 1.3125vw;
        letter-spacing: 0.365vw;
        line-height: 1.625vw;
        margin-bottom: 2.5vw;
        opacity: 0;
        transform: translate3d(0, 1vw, 0); } }

.item-block {
    position: absolute;
    top: 30vw;
    left: 60.5vw;
    &__title-roll {
        position: absolute;
        top: 0;
        left: 0;
        height: 1.45vw;
        width: 23.325vw;
        overflow: hidden; }
    &__title {
        text-transform: uppercase;
        position: absolute;
        transform: translate3d(0, 100%, 0);
        color: $light-grey;
        font-family: $avenir-black;
        letter-spacing: 0.225vw;
        line-height: 1.465vw;
        font-size: 1.3125vw; }

    &__description-roll {
        position: absolute;
        top: 2.25vw;
        width: 23.125vw;
        height: 4.2625vw;
        overflow: hidden; }
    &__description {
        position: absolute;
        color: #777777;
        transform: translate3d(0 , 100%, 0);
        line-height: 1.5625vw; } }






