@import "helpers/variables";

.header {
    width: 100%;
    height: 100vh;
    position: relative;
    overflow: hidden;
    transform: translate3d(0, 0, 0); }

.header-bg {
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-image: url('../img/header-bg-2.jpg');
    background-position: center;
    background-size: cover; }

.header-bg:before {
    position: absolute;
    z-index: -2;
    content: "";
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: $grey;
    opacity: 0; }

.logo {
    position: absolute;
    z-index: 50;
    top: 2.5vw;
    left: 3.125vw;
    width: 4.375vw;
    height: 2.5vw;
    background-image: url('../img/logo.png');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat; }

.menu-icon {
    cursor: pointer;
    position: absolute;
    z-index: 300;
    top: 3.125vw;
    right: 3.125vw;
    height: 1.375vw;
    width: 1.625vw;
    &__item {
        position: absolute;
        left: 0;
        width: 100%;
        height: 0.1875vw;
        background-color: $white;
        transition: background-color .3s ease-in-out;
        &_top {
            top: 0; }
        &_center {
            top: 44%; }
        &_bottom {
            bottom: 0; } } }

.pagination {
    position: relative;
    position: fixed;
    z-index: 10;
    top: 50%;
    left: 3.125vw;
    transform: translateY(-50%); }

.pagination-number {
    display: block;
    font-size: 3.75vw;
    font-family: $avenir-black;
    color: $white;
    margin-bottom: 2.6vw;
    transition: color .3s ease-in-out; }

.pagination-list {
    position: relative; }

.pagination-item {
    position: relative;
    display: block;
    margin-bottom: 1.575vw;
    position: relative;
    height: 1vw;
    width: 1vw;
    border-radius: 50%;
    background-color: $light-grey;
    margin: auto;
    margin-bottom: 1.575vw;
    transform: translate3d(0,0,0);
    &__line {
        height: 6.25vw;
        width: 0.125vw;
        background-color: $white;
        margin: auto;
        transform: translate3d(0, 0, 0);
        transform: scaleY(0);
        display: none; }
    &__label {
        pointer-events: none;
        display: block;
        width: 6vw;
        position: absolute;
        top: 0vw;
        text-transform: uppercase;
        font-family: $avenir-black;
        color: $white;
        left: 8vw;
        transform: translate3d(0vw, 0, 0);
        opacity: 0;
        transition: all .4s ease-out;
        letter-spacing: 0.125vw; } }

.icon-arrow {
    display: inline-block;
    vertical-align: middle;
    position: relative;
    top: -0.16vw;
    font-size: 1.2vw;
    color: $orange;
    transform: translate3d(0, 0, 0);
    transition: transform .3s ease-out; }

.header-content {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    padding-left: 25.75vw;
    &__line {
        width: 14.375vw;
        margin-top: 2.25vw;
        margin-bottom: 1.875vw;
        height: 0.1875vw;
        background-color: $orange; }
    &__link {
        display: inline-block;
        font-family: $avenir-heavy;
        color: $white;
        text-decoration: none;
        text-transform: uppercase;
        letter-spacing: 0.0625vw;
        margin-right: 1vw; }
    &__link:hover~ .icon-arrow {
        transform: translate3d(1vw, 0, 0); }
    &__title {
        width: 32vw;
        height: 4.1375vw;
        overflow: hidden;
        color: $white; } }

.title-item {
    display: inline-block; }
.social-media {
    position: absolute;
    top: 50%;
    right: 3.125vw;
    transform: translateY(-50%);
    &__item {
        width: 1.5vw;
        padding: 1.4vw 0; }
    &__link {
        text-decoration: none;
        color: $light-grey;
        width: 3vw;
        & svg {
            fill: $light-grey;
            font-size: 1.25vw; } }
    &__item:nth-child(1) {
        & svg {
            position: relative;
            right: -0.225vw; } }
    &__item:nth-child(4) {
        & svg {
            position: relative;
            right: 0.425vw; } } }

.icon-behance {
    width: 2vw; }

.mouse-block {
    position: absolute;
    z-index: 1;
    bottom: 0;
    left: 31.25vw;
    &__mouse-icon {
        background-image: url('../img/mouse-icon.png');
        background-position: center;
        background-size: cover;
        height: 1.875vw;
        width: 1.25vw;
        margin-bottom: 1.25vw; }
    &__line {
        height: 10.375vw;
        width: 0.1875vw;
        background-color: $light-grey;
        margin: auto; } }

.nav-mobile {
    &__list {
        position: absolute;
        top: 20%;
        left: 20%; }
    &__item {
        padding: 1vw;
        transform: translate3d(90vw, 0, 0); }
    &__link {
        color: $white;
        text-decoration: none;
        font-size: 2vw;
        font-family: $avenir-black;
        text-transform: uppercase; } }







