.blog {
    position: relative;
    background-color: $white;
    height: 100vh;
    width: 100%;
    &__title-roller {
        height: 40px;
        margin-bottom: 40px;
        overflow: hidden; }
    &__title-item {
        line-height: 40px;
        display: inline-block;
        color: $dark;
        font-family: $avenir-black;
        text-transform: uppercase;
        font-size: 42px;
        letter-spacing: 1.3vw;
        transform: translate3d(0, 40px, 0); }
    &__title-item:nth-child(6) {
        margin-right: 1.8vw; }
    &__line {
        width: 400px;
        height: 3px;
        background-color: $orange;
        margin-bottom: 100px;
        transform-origin: 0 100%;
        transform: scaleX(0); }
    &__image {
        width: 25vw;
        position: relative;
        img {
            max-width: 100%; } }
    &__image-overlay {
        position: absolute;
        content: '';
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: $white;
        transform: scaleX(1);
        transform-origin: 100% 100%; } }

.blog-article {
    position: absolute;
    top: 50%;
    left: 21.25vw;
    &__line {
        position: absolute;
        top: 0;
        left: 0;
        width: 6.25vw;
        height: 0.1875vw;
        background-color: $orange;
        transform-origin: 0 100%;
        transform: scaleX(0); }
    &__list-roller {
        position: absolute;
        top: -1vw;
        left: 8.25vw;
        width: 17.625vw;
        margin-bottom: 1vw; }
    &__title-roller {
        height: 1.6625vw;
        overflow: hidden;
        margin: 0.625vw; }
    &__item {
        display: inline-block;
        color: $dark;
        font-family: $avenir-black;
        text-transform: uppercase;
        font-size: 1.6875vw;
        transform: translate3d(0, 1.7vw, 0); }
    &__description {
        margin-top: 1vw;
        color: $light-grey;
        opacity: 0;
        transform: translate3d(0, -1vw, 0); } }

.blog-content {
    position: absolute;
    top: 10%;
    left: 24.125vw; }

.blog-item-list {
    position: absolute;
    top: 50%;
    left: 73.25vw; }

.blog-item {
    margin-bottom: 100px;
    &__title-roll {
        width: 270px;
        height: 22px;
        overflow: hidden;
        margin-bottom: 20px; }
    &__title {
        display: inline-block;
        color: $dark;
        font-family: $avenir-black;
        text-transform: uppercase;
        font-size: 21px;
        transform: translate3d(0, 23px, 0); }
    &__description {
        color: $light-grey;
        font-size: 14px;
        transform: translate3d(0, 14px, 0);
        opacity: 0; } }


