.contacts {
    width: 100%;
    height: 100vh;
    display: flex; }

.left {
    position: relative;
    width: 45%;
    height: 100%; }

.left-content {
    position: absolute;
    right: 3vw;
    top: 10%; }

.contacts-title {
    color: $white;
    text-transform: uppercase;
    font-family: $avenir-black;
    font-size: 42px;
    letter-spacing: 16px;
    margin-bottom: 35px;
    overflow: hidden;
    &__item {
        display: inline-block;
        transform: translate3d(0, 42px, 0); } }

.contacts-line {
    width: 400px;
    height: 3px;
    background-color: $orange;
    margin-bottom: 195px;
    transform-origin: 0 100%;
    transform: scaleX(0); }

.contacts-list {
    padding-left: 100px; }

.contacts-item {
    position: relative;
    margin: 20px 0;
    &__title-roll {
        overflow: hidden;
        height: 15px;
        margin-bottom: 5px; }
    &__title {
        display: inline-block;
        line-height: 15px;
        color: $light-grey;
        text-transform: uppercase;
        font-family: $avenir-black;
        transform: translate3d(0, 15px, 0); }
    &__label-roll {
        height: 30px;
        overflow: hidden; }
    &__label {
        display: inline-block;
        color: #777777;
        font-size: 14px;
        line-height: 30px;
        transform: translate3d(0, 30px, 0); } }

.icon-mail {
    color: $light-grey;
    position: absolute;
    top: -3px;
    left: -50px;
    font-size: 25px; }

.icon-phone-solid {
    color: $light-grey;
    position: absolute;
    top: -3px;
    left: -50px;
    font-size: 25px; }

.icon-map-marker {
    color: $light-grey;
    position: absolute;
    top: -3px;
    left: -50px;
    font-size: 25px; }

.right {
    position: relative;
    width: 55%;
    height: 100vh; }

.map-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: $white;
    z-index: 10;
    transform: translate3d(0, 0, 0);
    transform: scaleX(1);
    transform-origin: 100% 100%; }

.form {
    position: absolute;
    left: 3.68vw;
    bottom: 100px;
    background-color: $white;
    height: 520px;
    width: 630px;
    padding: 50px 70px; }

.form-group-inline {
    display: flex;
    margin-bottom: 32px;
    justify-content: space-between; }

.form-control {
    width: 47%;
    height: 47px;
    border: 2px solid #ededed;
    padding-left: 20px;
    color: $light-grey;
    letter-spacing: 0.25px; }

.form-control:placeholder {
    color: $light-grey; }

.form-control-lg {
    width: 100%;
    height: 47px;
    border: 2px solid #ededed;
    padding-left: 20px;
    color: $light-grey;
    letter-spacing: 0.25px; }

.form-group {
    margin-bottom: 32px; }

.text-control {
    width: 100%;
    height: 175px;
    padding: 20px 20px;
    color: $light-grey;
    letter-spacing: 0.25px;
    font-family: $lato;
    border: 2px solid #ededed;
    resize: none; }

.btn {
    height: 50px;
    width: 170px;
    background-color: #ff4200;
    color: $white;
    text-transform: uppercase;
    font-weight: 700;
    letter-spacing: 0.2px; }

.icon {
    opacity: 1; }

.icon-mail {
    opacity: 0; }

.icon-map-marker {
    opacity: 0; }

.icon-phone-solid {
    opacity: 0; }










