.loader {
    //display: none
    position: fixed;
    z-index: 200;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0; }
.overlay {
    display: none;
    position: absolute;
    z-index: 200;
    height: 100%;
    width: 100%;
    background-color: $white;
    transform: translate3d(0,0,0); }

.percents {
    position: absolute;
    z-index: 200;
    font-size: 5.625vw;
    font-family: $avenir-black;
    color: $black;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); }


