@import "helpers/variables";

body {
    font-family: $lato;
    font-weight: 400;
    font-size: 0.875vw; }

.line-active {
    display: block !important;
    transform: scaleY(1) !important; }

.title {
    font-family: $avenir-black;
    font-size: 4.5vw;
    text-transform: uppercase;
    letter-spacing: 1.525vw;
    line-height: 3.75vw; }

.subtitle {
    font-size: 1.3125vw;
    line-height: 3.75vw;
    font-weight: 700;
    letter-spacing: 0.3175vw;
    text-transform: uppercase;
    color: $light-grey; }

.dot-active {
    position: absolute;
    background-color: $white;
    display: block;
    height: 1vw;
    width: 1vw;
    border-radius: 50%;
    top: 0;
    left: 1.75vw;
    transform: translate3d(0, 0, 0); }

.app {
    overflow: hidden;
    position: absolute;
    opacity: 1;
    z-index: 250;
    width: 100%;
    height: 100vh;
    top: 0;
    left: 0;
    transform: scale(0); }

.main-layout {
    transform: translate3d(0, 0, 0);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%; }

.nav-mobile {
    z-index: 200;
    position: fixed;
    top: 0;
    right: -100%;
    width: 100%;
    height: 100%;
    background-color: $white;
    transform: translate3d(0, 0, 0);
    background-color: $black; }

.menu-active {
    background-color: $white !important; }











