.studio {
    height: 100vh;
    padding: 8.325vw 0;
    transform: translate3d(0, 0, 0); }

.studio-content {
    position: relative;
    width: 75vw;
    margin: auto;
    height: 100%; }
.studio-title {
    position: absolute;
    top: 35%;
    left: 0;
    width: 100%;
    height: 5vw;
    overflow: hidden;
    &__item {
        display: inline-block;
        transform: translate3d(0, 5vw ,0); }
    &_orange {
        display: inline-block;
        color: $orange;
        margin-right: 5vw; }
    &_dark {
        display: inline-block;
        color: $dark; } }

.studio-image {
    position: absolute;
    z-index: -1;
    top: 0;
    transform: translate3d(0, 0, 0);
    &_1 {
        width: 25vw;
        height: 25vw;
        right: 16.25vw;
        img {
            max-width: 100%; } }
    &_2 {
        width: 25vw;
        height: 25vw;
        top: 12.5vw;
        right: 0;
        img {
            max-width: 100%; } }
    &__overlay {
        width: 100%;
        height: 100%;
        background-color: $white;
        position: absolute;
        top: 0;
        left: 0;
        transform: translate3d(0 ,0, 0);
        transform: scaleX(1);
        transform-origin: 100% 100%; } }

.studio-description {
    position: absolute;
    bottom: 0;
    left: 6.25vw;
    color: $light-grey;
    line-height: 1.5vw;
    letter-spacing: 0.025vw;
    transform: translate3d(0, 0, 0);
    &__item {
        display: block;
        transform: translate3d(0, 2vw, 0); } }


.studio-line {
    position: absolute;
    left: 6.25vw;
    bottom: 6.75vw;
    width: 25vw;
    height: 0.1875vw;
    background-color: #ededed; }

.studio-txt-roller {
    height: 1.4vw;
    overflow: hidden; }



